<h1 id="laboratory-work" tabindex="-1"><a class="header-anchor" href="#laboratory-work" aria-hidden="true">#</a> Laboratory Work</h1>
<p>On this page you find information about the examination test <code>Laboratory Work</code>.</p>
<CompactInfo :infoPieces='{
    "Number of credits": "1.5",
    "Grades": ["Fail", "Pass"],
    "How to work": "Individually",
    "Goal": "To learn the basics in Android Development.",
    "Instructions": "Complete and present your solution to the lab described on this page to a teacher at a lab session.",
