}'>
</CompactInfo>
<div class="custom-container warning"><p class="custom-container-title">Help, don't cheat!</p>
<p>The laboratory work is individual work.</p>
<p><strong>It is OK to help each other</strong>. Examples of that:</p>
<ul>
<li>help your friend to debug his code</li>
<li>explain to your friend why his code doesn't work the way he thinks it works</li>
<li>compare and discuss different solutions to a lab problem with your friend after both of you have solved the lab to figure out which solution is the best one</li>
</ul>
<p><strong>It is NOT OK to cooperate in any way</strong>. Examples of that:</p>
<ul>
<li>sit at the same computer and write code together with your friend</li>
<li>sit at different computers and discuss and write the same code as your friend</li>
<li>give code to/receive code from your friend</li>
</ul>
<p><strong>You must write your own code! If you are unsure about what counts as <em>helping</em> and what counts as <em>cheating</em>, then simply work alone and ask only the teacher at the lab sessions for help.</strong></p>
</div>
<h2 id="what-you-need-to-know" tabindex="-1"><a class="header-anchor" href="#what-you-need-to-know" aria-hidden="true">#</a> What you need to know</h2>
<p>Before you start working on the lab, you are recommended to view/read at least the following lectures:</p>
<ul>
<li><RouterLink to="/lectures/java/">Java</RouterLink> (even if most new Android applications are implemented din Kotlin, many old applications are still implemented in Java)</li>
<li><RouterLink to="/lectures/kotlin/">Kotlin</RouterLink></li>
<li><RouterLink to="/lectures/android-design-patterns/">Android Design Patterns</RouterLink> (kind of repetition from your OOP courses)</li>
<li><RouterLink to="/lectures/android-introduction/">Android Introduction</RouterLink></li>
<li><RouterLink to="/lectures/android-fundamental-application-components/">Android Fundamental Application Components</RouterLink></li>
<li><RouterLink to="/lectures/android-activities/">Android Activities</RouterLink></li>
<li><RouterLink to="/lectures/android-views-and-layouts/">Android Views and Layouts</RouterLink></li>
<li><RouterLink to="/lectures/android-linear-layout/">Android Linear Layout</RouterLink></li>
<li><RouterLink to="/lectures/android-constraint-layout/">Android Constraint Layout</RouterLink></li>
<li><RouterLink to="/lectures/android-list-view/">Android ListView</RouterLink></li>
</ul>
<p>These recorded lectures, together with the first lecture and the first tutorial, should teach you most of what you need to use to complete the lab. Feel free to use more advanced views to implement a better graphical user interface.</p>
<h2 id="installing-required-software" tabindex="-1"><a class="header-anchor" href="#installing-required-software" aria-hidden="true">#</a> Installing Required Software</h2>
<p>In this course, we will only use Android Studio and the tools that come with it to create Android applications.</p>
<h3 id="on-the-school-s-computers" tabindex="-1"><a class="header-anchor" href="#on-the-school-s-computers" aria-hidden="true">#</a> On the school's computers</h3>
<p><strong>If you work on a computer in E1206, E2432 or E2433</strong>, all required software should already be installed for you, so no need to install anything yourself.</p>
<p><strong>If you work on a school computer in any other room</strong>, you need to manually install the required software through the <em>Software Center</em> application (unless someone already has done that on the specific computer you are using).</p>
<h3 id="on-your-own-computer" tabindex="-1"><a class="header-anchor" href="#on-your-own-computer" aria-hidden="true">#</a> On your own computer</h3>
<p><strong>If you work on your own private computer</strong>, you need to download and install all required software yourself. In this course all required software is available for free for both Windows, Mac and Linux.</p>
<p>Just follow the instructions at <a href="https://developer.android.com/studio/index.html" target="_blank" rel="noopener noreferrer">https://developer.android.com/studio/index.html<OutboundLink/></a> to download and install Android Studio.</p>
<p>To test the android applications you create, you need to run them on an Android device. If you have your own smartphone or tablet running Android you can use that one, just connect it to your computer using a USB cable.</p>
<div class="custom-container tip"><p class="custom-container-title">For Windows users</p>
<p>On Windows you need to <a href="https://developer.android.com/studio/run/oem-usb.html" target="_blank" rel="noopener noreferrer">Install OEM USB drivers<OutboundLink/></a> to be able to install Android applications on your connected Android device.</p>
</div>
<p>To more easily debug applications when they run on your own Android device, you need to <a href="https://developer.android.com/studio/debug/dev-options.html" target="_blank" rel="noopener noreferrer">Configure on-device developer options<OutboundLink/></a>.</p>
<p>If you don't have your own Android device to run your Android applications on, you can use an Android emulator that comes with Android Studio. If the emulator has not automatically been configured to take advantage of VM acceleration when Android Studio was installed, we highly recommend you to configure it to take advantage of the speed improvements (so the emulator lag less) by <a href="https://developer.android.com/studio/run/emulator-acceleration#accel-vm" target="_blank" rel="noopener noreferrer">Configuring VM acceleration<OutboundLink/></a>. This is a good idea even if you have a physical Android device to test on, because you probably want to test run your Android applications on different versions of Android, different screen sizes, etc., which is easy to do on an emulator, but impossible to do on a physical device.</p>
<div class="custom-container warning"><p class="custom-container-title">Note!</p>
<p>Getting VM acceleration to work is sometimes hard. If the emulator for some reason can't use it, it's hard to debug the reason. For example, you might get the error message <code>This computer meets requirements for HAXM, but VT-x is not turned on</code>, and the problem could be that <a href="https://stackoverflow.com/questions/21635504/error-during-installing-haxm-vt-x-not-working" target="_blank" rel="noopener noreferrer">an antivirus program doesn't allow it to be used<OutboundLink/></a>. Your development experience will be much better if you manage to enable VM acceleration, but it's probably not worth spending 20 hours on trying to get it to work.</p>
</div>
<p>Android Studio often reads from and writes to many files, such as when you create a new Android Studio project or when you build your application. To be efficient, it's important that reading from and writing to files is fast. There are some known cases slowing down this process, such as:</p>
<ul>
<li>Antivirus programs you have installed</li>
<li>Windows Defender</li>
<li>Windows File Indexing</li>
</ul>
<p>Nowadays Android Studio might install exceptions to these programs, improving the build performance, but if Android Studio is slow, you might want to investigate this.</p>
<div class="custom-container tip"><p class="custom-container-title">For Windows users</p>
<p>You can often find out what's slowing down Android Studio by using the <a href="https://www.howtogeek.com/108742/how-to-use-the-new-task-manager-in-windows-8/" target="_blank" rel="noopener noreferrer">Task Manager<OutboundLink/></a> to find the process that allocates much of your computer resources when Android Studio is slow, and then try to configure it to ignore Android Studio's folders. For example, see <a href="https://stackoverflow.com/a/49336163/2104665" target="_blank" rel="noopener noreferrer">configure Windows Defender and Windows File Indexing to ignore Android Studio folders<OutboundLink/></a>.</p>
</div>
<p>A fast CPU and an SSD instead of an HDD will of course also make Android Studio run faster as well; running Android Studio on a weak laptop usually gives you a very bad development experience.</p>
<h2 id="the-assignment" tabindex="-1"><a class="header-anchor" href="#the-assignment" aria-hidden="true">#</a> The Assignment</h2>
<p>The Graphical User Interface (GUI) for an Android application is to a large extent structured the same way as for a website (which you probably are acquaintance with).</p>
<table>
<thead>
<tr>
<th>Website</th>
<th>Android application</th>
</tr>
</thead>
<tbody>
<tr>
<td>Consists of multiple webpages</td>
<td>Consists of multiple activities</td>
</tr>
<tr>
<td>One webpage is shown at a time</td>
<td>One activity is shown at a time</td>
</tr>
<tr>
<td>URIs are used to identify webpages</td>
<td>Intents are used to identify activities</td>
</tr>
</tbody>
</table>
<p>When designing the GUI for an Android application it is hence important to think of which activities it should consist of. Traditionally, websites have been designed to be used by desktop computers with a large screen, and it's not uncommon to see a webpage you can do multiple things on, such as a webpage with both a &quot;sign in&quot; form, a &quot;sign up&quot; form and a &quot;I forgot my password&quot; form.</p>
<p>Android applications on the other hand often run on devices with a small screen, and there's not enough room to display more than one functionality at a time. Hence one functionality per activity is a good guideline, which you should follow in this lab.</p>
<div class="custom-container warning"><p class="custom-container-title">Note</p>
<p>With the introduction of fragments in Android API level 11 (Android 3), the guideline <em>One functionality per activity</em> is not that accurate since an entire application can consist of a single activity that changes which fragments it's displaying, and each fragment provides a functionality of some kind. Hence, <em>One feature per screen</em> might be a more accurate guideline.</p>
</div>
<p>In this lab you should implement a simple ToDo app, in which users can create, view, edit and delete ToDo items. Start by creating a new project in Android Studio. To start easy, add an <code>Empty Activity</code> initially, only support the latest version of Android and use Kotlin as your programming language (feel free to use Java if you want, but the code mentioned below on this page is Kotlin code).</p>
<p>A ToDo item will be represented using the <code>ToDo</code> class shown in <FigureNumber /> below. That code can be put in a new Kotlin file named <code>Todo.kt</code>, or similar.</p>
<Figure caption="The ToDo class.">
<div class="language-kotlin ext-kt line-numbers-mode"><pre v-pre class="language-kotlin"><code><span class="token keyword">package</span> XXX

<span class="token keyword">data</span> <span class="token keyword">class</span> <span class="token function">ToDo</span><span class="token punctuation">(</span>
    <span class="token keyword">val</span> id<span class="token operator">:</span> Int<span class="token punctuation">,</span>
    <span class="token keyword">var</span> title<span class="token operator">:</span> String<span class="token punctuation">,</span>
    <span class="token keyword">var</span> content<span class="token operator">:</span> String
<span class="token punctuation">)</span><span class="token punctuation">{</span>

    <span class="token keyword">override</span> <span class="token keyword">fun</span> <span class="token function">toString</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token operator">=</span> title

<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div></Figure>
<p>The data in your application is optimally stored in the built in supported SQLite database, but to simplify this lab (which primarily is about using activities) we will instead store the ToDo items in a global variable as shown in <FigureNumber /> below.</p>
<Figure caption="The ToDo items are stored in a global variable.">
<div class="language-kotlin ext-kt line-numbers-mode"><pre v-pre class="language-kotlin"><code><span class="token keyword">package</span> XXX

<span class="token comment">// Global variable used to store all ToDos.</span>
<span class="token keyword">val</span> toDoRepository <span class="token operator">=</span> <span class="token function">ToDoRepository</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">.</span><span class="token function">apply</span> <span class="token punctuation">{</span>
    <span class="token comment">// Let's add two initial ToDos.</span>
    <span class="token function">addToDo</span><span class="token punctuation">(</span>
