    <span class="token keyword">fun</span> <span class="token function">addToDo</span><span class="token punctuation">(</span>title<span class="token operator">:</span> String<span class="token punctuation">,</span> content<span class="token operator">:</span> String<span class="token punctuation">)</span><span class="token operator">:</span> Int<span class="token punctuation">{</span>
        <span class="token keyword">val</span> id <span class="token operator">=</span> <span class="token keyword">when</span> <span class="token punctuation">{</span>
            toDos<span class="token punctuation">.</span><span class="token function">count</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token operator">==</span> <span class="token number">0</span> <span class="token operator">-></span> <span class="token number">1</span>
            <span class="token keyword">else</span> <span class="token operator">-></span> toDos<span class="token punctuation">.</span><span class="token function">last</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">.</span>id<span class="token operator">+</span><span class="token number">1</span>
        <span class="token punctuation">}</span>
        toDos<span class="token punctuation">.</span><span class="token function">add</span><span class="token punctuation">(</span><span class="token function">ToDo</span><span class="token punctuation">(</span>
            id<span class="token punctuation">,</span>
