        <span class="token punctuation">)</span><span class="token punctuation">)</span>
        <span class="token keyword">return</span> id
    <span class="token punctuation">}</span>

    <span class="token keyword">fun</span> <span class="token function">getAllToDos</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token operator">=</span> toDos

    <span class="token keyword">fun</span> <span class="token function">getToDoById</span><span class="token punctuation">(</span>id<span class="token operator">:</span> Int<span class="token punctuation">)</span> <span class="token operator">=</span>
        toDos<span class="token punctuation">.</span><span class="token function">find</span> <span class="token punctuation">{</span>
