        <span class="token punctuation">}</span>

    <span class="token keyword">fun</span> <span class="token function">deleteToDoById</span><span class="token punctuation">(</span>id<span class="token operator">:</span> Int<span class="token punctuation">)</span> <span class="token operator">=</span>
        toDos<span class="token punctuation">.</span><span class="token function">remove</span><span class="token punctuation">(</span>
            toDos<span class="token punctuation">.</span><span class="token function">find</span> <span class="token punctuation">{</span>
