            <span class="token punctuation">}</span>
        <span class="token punctuation">)</span>

    <span class="token keyword">fun</span> <span class="token function">updateToDoById</span><span class="token punctuation">(</span>id<span class="token operator">:</span> Int<span class="token punctuation">,</span> newTitle<span class="token operator">:</span> String<span class="token punctuation">,</span> newContent<span class="token operator">:</span> String<span class="token punctuation">)</span><span class="token punctuation">{</span>

        <span class="token function">getToDoById</span><span class="token punctuation">(</span>id<span class="token punctuation">)</span><span class="token operator">?</span><span class="token punctuation">.</span><span class="token function">run</span><span class="token punctuation">{</span>
