mber">51</span><br><span class="line-number">52</span><br><span class="line-number">53</span><br><span class="line-number">54</span><br><span class="line-number">55</span><br><span class="line-number">56</span><br></div></div></Figure>
<p>Now we have the handling of the data in the application done, now we just need to build the graphical user interface on top of this to allow the user to see and modify the data. The final application should be as shown in <FigureNumber /> below.</p>
<Figure caption="Overview of the GUI.">
<p><img src="@source/courses/android-development/files/laboratory-work-gui.jpeg" alt="Overview of the GUI."></p>
</Figure>
<p>You should support at least 2 languages (English + another one), but the data (title and content of the ToDo items) will of course only be available in one language.</p>
<h3 id="mainactivity" tabindex="-1"><a class="header-anchor" href="#mainactivity" aria-hidden="true">#</a> MainActivity</h3>
<p>The <code>MainActivity</code> should list the titles of all ToDo items and have a CREATE button. Clicking on one of the titles should start the <code>ViewToDoActivity</code> that displays more information about the ToDo that was clicked (pass the id of the clicked ToDo item in the intent), and clicking on the CREATE button should start the <code>CreateToDoActivity</code>.</p>
<p>The layout for the <code>MainActivity</code> can be a <a href="https://developer.android.com/reference/android/support/constraint/ConstraintLayout" target="_blank" rel="noopener noreferrer">ConstraintLayout<OutboundLink/></a> that displays a <a href="https://developer.android.com/guide/topics/ui/controls/button" target="_blank" rel="noopener noreferrer">Button<OutboundLink/></a> at the bottom of the layout and a <a href="https://developer.android.com/reference/android/widget/ListView" target="_blank" rel="noopener noreferrer">ListView<OutboundLink/></a> in the rest of the space above it. The <code>ListView</code> will in turn display the title of each ToDo item. To do that, you can use code like the one shown <FigureNumber /> below in the Activity's <code>onCreate()</code> (you need to add a <code>&lt;ListView&gt;</code> to your layout with appropriate attributes).</p>
<Figure caption="Adding an ArrayAdapter to a ListView.">
<div class="language-kotlin ext-kt line-numbers-mode"><pre v-pre class="language-kotlin"><code><span class="token keyword">val</span> listView <span class="token operator">=</span> theActivity<span class="token punctuation">.</span>findViewById<span class="token operator">&lt;</span>ListView<span class="token operator">></span><span class="token punctuation">(</span>R<span class="token punctuation">.</span>id<span class="token punctuation">.</span>list_view<span class="token punctuation">)</span>
listView<span class="token punctuation">.</span>adapter <span class="token operator">=</span> ArrayAdapter<span class="token operator">&lt;</span>ToDo<span class="token operator">></span><span class="token punctuation">(</span>
    aContext<span class="token punctuation">,</span>
    android<span class="token punctuation">.</span>R<span class="token punctuation">.</span>layout<span class="token punctuation">.</span>simple_list_item_1<span class="token punctuation">,</span>
    android<span class="token punctuation">.</span>R<span class="token punctuation">.</span>id<span class="token punctuation">.</span>text1<span class="token punctuation">,</span>
    toDoRepository<span class="token punctuation">.</span><span class="token function">getAllToDos</span><span class="token punctuation">(</span><span class="token punctuation">)</span>
<span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div></Figure>
<p>Then you also need to register a click listener on the <code>listView</code> for listening for clicks on the ToDo items in it.</p>
<h3 id="createtodoactivity" tabindex="-1"><a class="header-anchor" href="#createtodoactivity" aria-hidden="true">#</a> CreateToDoActivity</h3>
<p>The <code>CreateToDoActivity</code> should display a form through which the user can enter the title and content of a new ToDo item. When a new ToDo item has successfully been created the user should be taken to the <code>ViewToDoActivity</code> displaying the ToDo item the user just created (pass the id of the newly created ToDo item in the intent).</p>
<p>You can use an <a href="https://developer.android.com/reference/android/widget/EditText" target="_blank" rel="noopener noreferrer">EditText<OutboundLink/></a> to let the user enter som text. You can then use the code in <FigureNumber /> below to read out what text the user has entered in the <code>EditText</code>.</p>
<Figure caption="Reading out the text the user has entered in an EditText.">
<div class="language-kotlin ext-kt line-numbers-mode"><pre v-pre class="language-kotlin"><code><span class="token keyword">val</span> title <span class="token operator">=</span> theActivity<span class="token punctuation">.</span>findViewById<span class="token operator">&lt;</span>EditText<span class="token operator">></span><span class="token punctuation">(</span>
    R<span class="token punctuation">.</span>id<span class="token punctuation">.</span>title_edit_text
<span class="token punctuation">)</span><span class="token punctuation">.</span>editableText<span class="token punctuation">.</span><span class="token function">toString</span><span class="token punctuation">(</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div></Figure>
<p>Add some kind of validation so the user can't add bad ToDo items, such as a ToDo item with a too short/long title/content. Display descriptive error messages to the user if something is invalid, e.g. change the text in a <a href="https://developer.android.com/reference/android/widget/TextView" target="_blank" rel="noopener noreferrer">TextView<OutboundLink/></a>.</p>
<div class="custom-container tip"><p class="custom-container-title">Listen for user typing</p>
<p>If you want to listen for when the user types something in an <code>EditText</code> you can use a <a href="https://developer.android.com/reference/android/text/TextWatcher" target="_blank" rel="noopener noreferrer">TextWatcher<OutboundLink/></a>. This way you can validate the input as the user types it (i.e. give immediate feedback), and, for example, disable the SAVE button when the input is invalid, and enable it when the input is valid.</p>
</div>
<div class="custom-container tip"><p class="custom-container-title">Make it more beautiful</p>
<p>Instead of using an <code>EditText</code> you can use a <a href="https://developer.android.com/reference/com/google/android/material/textfield/TextInputLayout.html" target="_blank" rel="noopener noreferrer">TextInputLayout<OutboundLink/></a> together with a <code>TextInputEditText</code> to make it look a bit more beautiful.</p>
</div>
<div class="custom-container tip"><p class="custom-container-title">Improving back navigation</p>
<p>When the user has created a new ToDo item and ended up at the <code>ViewToDoActivity</code> and then clicks on the back button, the user probably wants to end up at the <code>MainActivity</code>, and not at the <code>CreateToDoActivity</code>. To make that happen, you can in the <code>CreateToDoActivity</code> call <code>finish()</code> after you have started the <code>ViewToDoActivity</code>.</p>
</div>
<div class="custom-container tip"><p class="custom-container-title">Updating the MainActivity</p>
<p>When the user navigates back to the <code>MainActivity</code> after having created a new ToDo item, you need to tell the <code>ListView</code> to re-render itself, since it's data source (the list with all ToDo items) has changed. You can do that by calling the <a href="https://developer.android.com/reference/android/widget/ArrayAdapter.html#notifyDataSetChanged()" target="_blank" rel="noopener noreferrer">notifyDataSetChanged()<OutboundLink/></a> method on your <code>ArrayAdapter</code> in the <code>MainActivity</code>'s <code>onStart()</code> method.</p>
</div>
<h3 id="viewtodoactivity" tabindex="-1"><a class="header-anchor" href="#viewtodoactivity" aria-hidden="true">#</a> ViewToDoActivity</h3>
<p>The <code>ViewToDoActivity</code> should display all information about a ToDo item with a specific id. This activity should define the name of an extra argument that can be put in the Intent starting this activity, specifying the id of the ToDo item to display information about. This can be put in the class's companion object, as shown in <FigureNumber /> below.</p>
<Figure caption="A companion object that can be put inside a class.">
<div class="language-kotlin ext-kt line-numbers-mode"><pre v-pre class="language-kotlin"><code><span class="token keyword">companion</span> <span class="token keyword">object</span> <span class="token punctuation">{</span>
    <span class="token keyword">const</span> <span class="token keyword">val</span> EXTRA_TODO_ID <span class="token operator">=</span> <span class="token string">"TODO_ID"</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div></Figure>
<p>The <code>ViewToDoActivity</code> activity should also contain an UPDATE button and a DELETE button. Clicking on the UPDATE button should start the <code>UpdateToDoActivity</code>, where the user can change the title and content of the currently displayed ToDo item. Clicking on the DELETE button should display a dialog where the user can confirm the she wants to delete the ToDo item. To display the dialog, you can use the code shown in <FigureNumber /> below.</p>
<Figure caption="Code showing a dialog.">
<div class="language-kotlin ext-kt line-numbers-mode"><pre v-pre class="language-kotlin"><code><span class="token comment">// TODO: Don't hardcode strings in English.</span>
AlertDialog<span class="token punctuation">.</span><span class="token function">Builder</span><span class="token punctuation">(</span>aContext<span class="token punctuation">)</span>
    <span class="token punctuation">.</span><span class="token function">setTitle</span><span class="token punctuation">(</span><span class="token string">"Delete ToDo"</span><span class="token punctuation">)</span>
    <span class="token punctuation">.</span><span class="token function">setMessage</span><span class="token punctuation">(</span><span class="token string">"Do you really want to delete it?"</span><span class="token punctuation">)</span>
    <span class="token punctuation">.</span><span class="token function">setPositiveButton</span><span class="token punctuation">(</span>
        <span class="token string">"Yes"</span>
    <span class="token punctuation">)</span> <span class="token punctuation">{</span> dialog<span class="token punctuation">,</span> whichButton <span class="token operator">-></span>
        <span class="token comment">// Delete it.</span>
    <span class="token punctuation">}</span><span class="token punctuation">.</span><span class="token function">setNegativeButton</span><span class="token punctuation">(</span>
        <span class="token string">"No"</span>
    <span class="token punctuation">)</span> <span class="token punctuation">{</span> dialog<span class="token punctuation">,</span> whichButton <span class="token operator">-></span>
        <span class="token comment">// Do not delete it.</span>
    <span class="token punctuation">}</span><span class="token punctuation">.</span><span class="token function">show</span><span class="token punctuation">(</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div></Figure>
<p>If the user deletes the ToDo item, take the user back to the <code>MainActivity</code>.</p>
<div class="custom-container warning"><p class="custom-container-title">Runtime configuration changes not handled</p>
<p>The dialog in <FigureNumber previous /> above does not survive runtime configuration changes. For example, try showing the dialog and then rotate the screen. The dialog disappears since a runtime configuration change occurred, causing the activity to be destroyed and then re-created. Handled properly, the dialog should still be shown after the activity has been re-created, but handling that is not part of this lab.</p>
</div>
<div class="custom-container warning"><p class="custom-container-title">Text overflow</p>
<p>What happens if you have a ToDo item with a very long text (or use a device with a very small screen) so all text can't be shown on the screen at once? Can you scroll to see the rest of it? Nope, not unless you use a <a href="https://developer.android.com/reference/android/widget/ScrollView" target="_blank" rel="noopener noreferrer">ScrollView<OutboundLink/></a> or similar. Using that is always a good idea when you display text that you are not sure will fit on all screens (i.e. in most cases).</p>
</div>
<h3 id="updatetodoactivity" tabindex="-1"><a class="header-anchor" href="#updatetodoactivity" aria-hidden="true">#</a> UpdateToDoActivity</h3>
<p>You should be able to figure out how to implement the <code>UpdateToDoActivity</code> on your own. Good luck! 😀</p>
<h2 id="checklist" tabindex="-1"><a class="header-anchor" href="#checklist" aria-hidden="true">#</a> Checklist</h2>
<p>Before you present your work to a teacher, make sure that:</p>
<ul>
<li>At least two different languages are supported throughout the entire application</li>
<li>You validate the user input when creating and updating a ToDo item and display descriptive error messages if something is invalid</li>
<li>The back navigation always makes sense (especially after you have created, deleted or updated a ToDo item)</li>
<li>The data shown on the screen always is up to date (especially after you have created, deleted or updated a ToDo item)</li>
<li>The user always can properly use the GUI no matter how small screen the user has or how long texts the ToDo items contain</li>
<li>You understand how all code in your application works</li>
</ul>
<h2 id="present-your-work" tabindex="-1"><a class="header-anchor" href="#present-your-work" aria-hidden="true">#</a> Present your work</h2>
<p>Present your work to a teacher at one of the lab sessions. You should be able to explain how all code in your application works, and the teacher will ask you some questions about it to verify this. If the teacher is satisfied with your presentation he will approve you on the Canvas assignment <a href="https://ju.instructure.com/courses/4825/assignments/25231" target="_blank" rel="noopener noreferrer">The Lab<OutboundLink/></a> <strong>after</strong> you have uploaded your Android Studio project there.</p>
<h2 id="optional-exercises" tabindex="-1"><a class="header-anchor" href="#optional-exercises" aria-hidden="true">#</a> Optional exercises</h2>
<p>These exercises are not part of the laboratory work. Feel free to complete them to practice on using various techniques/concepts used in Android applications before you apply them in your project work. If you get stuck you can always ask the teacher at a lab session for help. When you're done, feel free to discuss your solution with a teacher at a lab session to get some feedback on your work.</p>
<h3 id="handling-runtime-configuration-changes" tabindex="-1"><a class="header-anchor" href="#handling-runtime-configuration-changes" aria-hidden="true">#</a> Handling runtime configuration changes</h3>
<p>In the <code>ViewToDoActivity</code>, when the activity starts, create a new instance of <a href="https://developer.android.com/reference/android/speech/tts/TextToSpeech" target="_blank" rel="noopener noreferrer">TextToSpeech<OutboundLink/></a> and use it to speak out loud the title and the content of the ToDo item.</p>
<p>When the user rotates the screen, a runtime configuration change will take place, causing the activity to be destroyed and re-created. This should be transparent to the user, but if you don't write special code handling that, you will in the re-created activity create a new instance of <code>TextToSpeech</code> again and tell it to speak out loud the title and the content of the ToDo item again. That should not happen.</p>
<p>And if the user chooses to leave the activity before your <code>TextToSpeech</code> instance is done saying the entire title and content, you need to tell your <code>TextToSpeech</code> instance to stop talking. As a result, your <code>TextToSpeech</code> instance needs to survive configuration changes. This can be done using a model fragment (a fragment with no GUI and that has been set to retain it's instance) (old solution) or using a ViewModel (modern solution).</p>
<div class="custom-container danger"><p class="custom-container-title">Think twice about the context</p>
<p>When creating a new instance of the <code>TextToSpeech</code> class you need to pass it a context. Using the activity as the context works, but if the activity is destroyed and re-created, the destroyed activity can't be garbage collected since the <code>TextToSpeech</code> instance holds a reference to it. Therefor (as in many other cases) it is better to pass it the context <code>anActivity.applicationContext</code> instead.</p>
</div>
<p>In the <code>ViewToDoActivity</code> you can also try to handle runtime configuration changes so the delete dialog appears as it should when the activity is re-created. To make that happen you should use a <a href="https://developer.android.com/reference/android/support/v4/app/DialogFragment.html" target="_blank" rel="noopener noreferrer">DialogFragment<OutboundLink/></a> to show the dialog.</p>
<h3 id="storing-data-in-a-database" tabindex="-1"><a class="header-anchor" href="#storing-data-in-a-database" aria-hidden="true">#</a> Storing data in a database</h3>
<p>Store data in the built-in supported SQLite database. Either do it the old way using <a href="https://developer.android.com/reference/android/database/sqlite/SQLiteOpenHelper" target="_blank" rel="noopener noreferrer">SQLiteOpenHelper<OutboundLink/></a>, or do it the more modern way using the <a href="https://developer.android.com/topic/libraries/architecture/room" target="_blank" rel="noopener noreferrer">Room Persistence Library<OutboundLink/></a>.</p>
<div class="custom-container danger"><p class="custom-container-title">Think twice about the context</p>
<p>To work with the SQLite database you need to pass it a context. If your database communication object outlives your activity (e.g. if you use the singleton pattern), using the activity as the context is bad, since the activity can't be garbage collected when the user leaves it, so rather (as usual) use <code>anActivity.applicationContext</code> as your context instead.</p>
</div>
<h3 id="storing-data-in-firestore" tabindex="-1"><a class="header-anchor" href="#storing-data-in-firestore" aria-hidden="true">#</a> Storing data in Firestore</h3>
<p>Store your data in <a href="https://firebase.google.com/docs/firestore/" target="_blank" rel="noopener noreferrer">Firestore<OutboundLink/></a>. Let the user create and login to a new account, and then store the ToDo items in Firestore. This way, it doesn't matter which device the user is using, she can access her ToDo items on all of them!</p>
<h3 id="using-a-recyclerview" tabindex="-1"><a class="header-anchor" href="#using-a-recyclerview" aria-hidden="true">#</a> Using a RecyclerView</h3>
<p>In <code>MainActivity</code>, instead of using a <code>ListView</code> to display the title of all ToDo items, use a <a href="https://developer.android.com/reference/android/support/v7/widget/RecyclerView" target="_blank" rel="noopener noreferrer">RecyclerView<OutboundLink/></a>. Allow the user to delete a ToDo item simply by swiping the title of the ToDo item to the side.</p>
<h3 id="using-notifications" tabindex="-1"><a class="header-anchor" href="#using-notifications" aria-hidden="true">#</a> Using Notifications</h3>
<p>Add a timestamp to each ToDo item representing a deadline for when that ToDo item should be completed. Then use the <a href="https://developer.android.com/reference/android/app/AlarmManager" target="_blank" rel="noopener noreferrer">AlarmManager<OutboundLink/></a> to create an Intent that is broadcasted each day (e.g. at 09:00; can potentially be configured by the user through a <code>SettingsActivity</code>), and then create a <a href="https://developer.android.com/reference/android/content/BroadcastReceiver" target="_blank" rel="noopener noreferrer">BroadcastReceiver<OutboundLink/></a> listening for this Intent, and when your receiver receives the intent, <a href="https://developer.android.com/guide/topics/ui/notifiers/notifications" target="_blank" rel="noopener noreferrer">display a notification<OutboundLink/></a> to the user with information about which ToDo items that needs to be completed within 24 hours.</p>
